/* Contact Section */
.contact-page {
  padding: 40px 20px;
  max-width: 800px;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.05);
}

.contact-page h2 {
  text-align: center;
  color: var(--color-primary);
  margin-bottom: 20px;
}

.contact-page p {
  text-align: center;
  color: var(--color-text-medium);
  margin-bottom: 30px;
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 40px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 20px;
