  line-height: 1.6;
}

.contact-buttons {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
}

.contact-button {
  padding: 12px 25px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
}

.contact-button.whatsapp {
  background: linear-gradient(135deg, #25d366 0%, #128c7e 100%);
