}

.contact-button.instagram {
  background: linear-gradient(135deg, #e4405f 0%, #c13584 100%);
  color: white;
}

.contact-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
}

.hours h4 {
  color: #6a4c93;
  margin-bottom: 10px;
}

.hours p {
  color: #666;
}

.map-placeholder {
  background: linear-gradient(135deg, #e6e6fa 0%, #add8e6 100%);
  border-radius: 20px;
  padding: 50px 30px;
  text-align: center;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.map-placeholder span {
  font-size: 4rem;
  margin-bottom: 20px;
}

.map-placeholder p {
