  }
  
  .about h2,
  .catshop h2,
  .contact h2 {
    font-size: 1.8rem;
  }
  
  .product-card {
    padding: 15px;
  }
  
  .container {
    padding: 0 10px;
  }
  
  .services-grid {
    grid-template-columns: 1fr;
  }
  
  .features {
    grid-template-columns: 1fr;
  }
  
  .contact-buttons {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 15px;
  }
  
  .hero-content h2 {
    font-size: 1.8rem;
  }
  
  .cat-emoji {
    font-size: 5rem;
  }
  
  .product-card {
    padding: 20px;
  }
  
  .nav button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Animation for sections */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

section {
  animation: fadeInUp 0.6s ease-out;
}
